.header {
  background-image: linear-gradient(
    269.46deg,
    $dark-color 20.66%,
    $main-color 85.22%
  );
}

.header__container {
  display: flex;

  // tablet
  @media screen and (min-width: 768px) {
    align-items: center;
    justify-content: space-between;
  }
}

.nav {
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.logo {

  animation: puff-in-center 0.7s cubic-bezier(0.470, 0.000, 0.745, 0.715) both;

  // tablet
  @media screen and (min-width: 768px) {
    margin: 22px 0;
  }

  // desktop
  @media screen and (min-width: 1280px) {
    margin: 22px 0;
  }
}

.logo__img {
  display: block;
  fill: #fcfcfb;
}

.nav__list {
  // tablet
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.nav__link {
  // tablet
  @media screen and (min-width: 768px) {
    color: $light-color;
    font-family: 'Inter';
    font-style: normal;
    font-size: 16px;
    line-height: calc(19 / 16);
  }
}

.nav__item:not(:last-child) {
  // tablet
  @media screen and (min-width: 768px) {
    margin-right: 32px;
  }

  // desktop
  @media screen and (min-width: 1280px) {
    margin-right: 48px;
  }
}

.btn-dark-desk {
  animation: wiggle 2s linear infinite 2s;

    &:hover, &:focus {
      animation: paused;
    }

  @media screen and (max-width: 1279px) {
    display: none;
  }
}


@keyframes puff-in-center {
  0% {
    -webkit-transform: scale(2);
    transform: scale(2);
    -webkit-filter: blur(2px);
    filter: blur(2px);
    opacity: 0;
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}


@keyframes wiggle {

  0%,
  7% {
    transform: rotateZ(0);
  }

  15% {
    transform: rotateZ(-15deg);
  }

  20% {
    transform: rotateZ(10deg);
  }

  25% {
    transform: rotateZ(-10deg);
  }

  30% {
    transform: rotateZ(6deg);
  }

  35% {
    transform: rotateZ(-4deg);
  }

  40%,
  100% {
    transform: rotateZ(0);
  }
}